    padding: 5px;
  }

  body .nft .about .content .info .advances .item-image__image img {
    width: 30px;
    height: auto;
  }

  body .nft .about .content .info .advances .item-image__text {
    font-size: .7rem;
  }

  body .nft .works .cards, body .nft .works .cards-item {
    margin-top: 25px;
  }

  body .nft .works .cards-item__tag {
    font-size: 1rem;
  }

  body .nft .roadmap .cards .item-top__circle span {
    font-size: .9rem;
  }

  body .nft .roadmap .cards .item__title, body .nft .faq .list-item__header {
    font-size: 1.1rem;
  }

  body .nft .discover__description {
    font-size: .9rem;
  }

  body .footer-links {
