  }
}

.button {
  color: #0f051d;
  cursor: pointer;
  background-color: #0000;
  border: 2px solid;
  border-radius: 20px;
  padding: 13px 25px;
  font-family: Space Grotesk, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  transition: all .5s;
}

.button:hover {
  color: #fff;
  background-color: #241b44;
}

.button:active {
  transform: translateY(5px);
}

.modal {
  z-index: 1;
  background: #0f051dcc;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal .item {
  background-color: #fff;
  border-radius: 25px;
  flex-direction: column;
  justify-content: center;
  width: 400px;
  height: auto;
  padding: 25px;
  display: flex;
  position: relative;
}

.modal .item__close {
  cursor: pointer;
  width: 35px;
  height: 35px;
  position: absolute;
  top: 15px;
  right: 15px;
}

.modal .item__title {
  color: #0f051d;
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
}

.modal .item .form {
  flex-direction: column;
  justify-content: center;
  margin-top: 25px;
  display: flex;
}

.modal .item .form__text, .modal .item .form__email {
  color: #0f051d;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #0f051d;
  outline: none;
  width: 100%;
  height: 50px;
  margin-top: 25px;
  font-family: Space Grotesk, sans-serif;
  font-size: 1.4rem;
  font-weight: 500;
}

.modal .item .form__text:first-child, .modal .item .form__email:first-child {
  margin-top: 0;
}

.modal .item .form__button {
  margin-top: 25px;
}

.hidden {
  display: none;
}

.overflowHidden {
  overflow-y: hidden;
}

.title {
  color: #0f051d;
  text-align: center;
  font-size: 3rem;
  font-weight: 700;
}

.header {
  z-index: 10;
  padding-left: 15px;
  padding-right: 15px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header .nav {
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
}

.header .nav-logo {
  color: #0f051d;
  font-size: 1.7rem;
  font-weight: 700;
}

.header .nav-menu {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header .nav-menu__item {
  margin-left: 45px;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all .8s;
}

.header .nav-menu__item a {
  color: #0f051d;
}

.header .nav-menu__item:hover {
  opacity: .5;
}

.header .nav-menu__item:first-child {
  margin-left: 0;
}

.header .nav .hamb {
  cursor: pointer;
  display: none;
}

.header .nav .hamb__bar {
  background-color: #0f051d;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  transition: all .3s ease-in-out;
  display: block;
}

.scroll-header-bg {
  background-color: #fff;
}

.hide-on-modal {
  z-index: 1;
}

